import React from 'react';
import './project.scss';
import './layout.scss';

const Project = props => (
    <div className="project">
        <div className="container project__border">
            <a href={props.link} target="blank"> <img className="project__img" src={props.image} alt=""/> </a>
            <a className="project__title" href={props.link} target="blank"> {props.title} </a>
            <p className="project__description"> {props.description} </p>
            <a className="project__link"  href={props.github} target="blank"> Code on GitHub &rarr; </a> 
        </div> 
    </div>
)

export default Project